.container {
    padding-left: 20px;
    padding-right: 20px;
    margin-left: auto;
    margin-right: auto;

    @media screen and (min-width: $mobile) {
      width: $mobile;
    }

    @media screen and (min-width: $tablet) {
      width: $tablet;
      padding-left: 32px;
      padding-right: 32px;
    }

    @media screen and (min-width: $desctop) {
      width: $desctop;
      padding-left: 16px;
      padding-right: 16px;
    }
  }