.header {
    background-color: transparent;
    padding-top: 12px;

    @media screen and (min-width: $tablet) {
            padding-top: 20px;
            }
    @media screen and (min-width: $desctop) {
              padding-top: 40px;
             }
    
}
.header__container {
    position: relative;
    display: flex;
    align-items: center;
}
.logo {
    position: absolute;
    width: 40px;
    height: auto;
    top: -10;
    left: 0;
    z-index: 16;

    @media screen and (min-width: $tablet) {
            width: 48px;
            }
    @media screen and (min-width: $desctop) {
              width: 60px;
              top: -23;
             }
}
.list {
    padding: 0;
    margin: 0;
    list-style: none;
}
.menu {
    display: none;
    margin-left: 308px;
    @media screen and (min-width: $desctop) {
        display: flex;
        gap: 28px;
        justify-content: center;
        align-items: center;
    }
}
.menu__link {
    font-weight: 700;
    font-size: 16px;
    line-height: calc(20 / 16);
    color: var(--secondary-text-color);
    text-decoration: none;
    padding-top: 32px;
    padding-bottom: 13px;
    transition-property: color;
    transition-duration: 250ms;
    transition-timing-function: var(--timing-function);
    &:hover,
    &:focus {
        color: var(--accent-color);
    }
}
.tablet-button {
    display: flex;
    gap: 32px;
    margin-left: auto;
    align-items: center;
}
.order {
    display: none;
    
    @media screen and (min-width: $tablet) {
        color: var(--primary-text-color);
        background: var(--accent-color);
        display: flex;
        width: 152px;
        height: 42px;
        border-radius: 10px;
        border: none;
        justify-content: center;
        align-items: center;
        font-weight: 600;
        font-size: 14px;
        line-height: calc(17 / 14);
        cursor: pointer;
        
    }
}
.mobile-button {
    display: block;
        width: 32px;
        height: 32px;
        border: none;
        background-color: transparent;
        
    @media screen and (min-width: $desctop) {
    display: none;   
    }
}
.mobile-menu {
    position: fixed;
    z-index: 15;
        top: 0;
        width: 100vw;
        height: 100vh;
        background-color: var(--primary-bg-color);
        @media screen and (min-width:$desctop) {
           display: none; 
        }
    }
.is-hidden {
    transition: 0.5s;
    transform: translateX(100%);
}
.is-active{
    transition: 0.5s;
    transform: translateX(0%);
}


