.mobile-menu-button {
    display: block;
    width: 32px;
    height: 32px;
    margin-top: 10px;
    margin-left: auto;
    border: none;
    background-color:transparent;
    position: absolute;
    top: 16px;
    right: 20px;
}
.mobile__navigation {
    display: flex;
    justify-content: center;
    text-align: center;
    margin-top: 76px;
    margin-bottom: 122px;
           
}
.navigation__item:not(:last-child) {
    margin-bottom: 32px;
}
.navigation__link {
    font-weight: 700;
    font-size: 32px;
    line-height: calc(39 / 32);
    color: var(--modal-bg-color);
    text-decoration: none;
    @media screen and (min-width: $tablet) {
        font-size: 48px;
        line-height: calc(59 / 48);
    }
    &:focus {
        color: var(--primary-text-color);
    }
}
.mobile-order{
    display: flex;
    justify-content: center;
    text-align: center;
    margin-bottom: 64px;
    }
.mob-order {
        width: 152px;
            height: 42px;
            background: var(--accent-color);
            border-radius: 10px;
            border: none;
            justify-content: center;
            align-items: center;
        
            font-weight: 600;
            font-size: 14px;
            line-height: calc(17 / 14);
            color: var(--primary-text-color);
            @media screen and (min-width: $tablet){
            display: none
              }
}
